body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  background-color: #111;
  color: white;
  width: 100vw;
}

nav {
  display: flex;
  grid-gap: 3rem;
  gap: 3rem;
  width: 100%;
  height: 9.5rem;
}

.navItem {
  width: 10rem;
  height: 100%;
  font-size: 1.5rem;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  color: white;
}

.navItem > hr {
  width: 80%;
  height: 2px;
  background-color: white;
  margin: 0;
}

.navItem > h1 {
  margin:  0;
}

.navItem:hover > hr {
  margin-top: 0.25rem;
}

.logo {
  font-size: 2rem;
  margin: 1rem 2rem 1rem 2rem;
  text-align: left;
}

.logo > h1 {
  margin: 0;
  line-height: 80%;
}

.home {
  width: 100%;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  grid-gap: 4rem;
  gap: 4rem;
}

.leaderboard {
  flex: 1 1;
  padding: 0 15% 0 15%;
  display: flex;
  flex-direction: column;
}

.games {
  flex: 2 1;
}

th, td {
  padding: 0.25rem;
  font-size: 1.25rem;
}

th {
  text-align: left;
  background-color: #222;
}

.AddGame {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

input {
  border: 1px solid white;
  border-radius: 1rem;
  padding: 0.5rem;
  background-color: #222;
  font-size: 1.25rem;
  color: white;
}
